import React from 'react';

const Card = (props) => {
  return (
    <div className='bg-light-green dib br3 pa3 ma2 grow bw2 shadow-5'>
      <img src={`https://robohash.org/${props.id}`} alt='' />
      <h1>{props.name}</h1>
      <h2>{props.email}</h2>
    </div>
  );
};

export default Card;
